import React , { Component } from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import { Link } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import styles from './Style/ColorBoxStyle'


class ColorBox extends Component{
    constructor(props){
        super(props);
        this.state = { copied: false };
        this.changeCopyState = this.changeCopyState.bind(this);
    }
    changeCopyState(){
        this.setState({ copied: true }, ()=> {
            setTimeout(() => this.setState({ copied: false }), 1500);
        });
    }
    render(){
        const {name, background, paletteId, id, showFullPalette, classes} = this.props;
        const { copied } = this.state;

        return (
            <CopyToClipboard text={background} onCopy={this.changeCopyState}>
            <div style={{background}} className={classes.colorBox}>
            <div style={{background}} className={`${classes.copyOverlay} ${copied && classes.showOverlay}`}></div>
              <div className={`${classes.CopyMesssage} ${copied && classes.showCopyMeaasge}`}>
                <h1>COPIED!</h1>
                <p className={classes.copyText}>{this.props.background}</p>
              </div>
              <div>
              <div className={classes.boxContent}>
                <span className={classes.colorName}>{name}</span>
              </div>
              <button className={classes.copyButton}>COPY</button>
              </div>
              {showFullPalette &&
              <Link to={`/palette/${paletteId}/${id}`} onClick={e => e.stopPropagation()}>
              <span className={classes.seeMore}>MORE</span>
              </Link>
               }
            </div>
            </CopyToClipboard>
        )
    }
}

export default withStyles(styles)(ColorBox);